@import '../../assets/styles/h3Imports';

html {
  --search-width: rem(100);
  --search-height: rem(48);
}

.search {
  display: inline-flex;
  max-width: var(--search-width);
  vertical-align: middle;
  width: var(--search-width);

  &.focus {
    border-radius: 8px;
    outline: 2px solid var(--search-outline-focus);
    outline-offset: -2px;
  }

  &.hover {
    border-radius: 8px;
    outline: 2px solid var(--search-outline-focus);
    outline-offset: -2px;
  }

  .button {
    background-color: white;
    border: none;
    border-radius: 8px 0 0 8px;
    border-right: rem(1) solid var(--search-outline-focus);
    min-height: rem(48);
    min-width: rem(48);
    vertical-align: middle;

    --icon-stroke: var(--search-outline-focus);

    &:focus-visible {
      outline: 2px solid var(--search-outline-focus);
      z-index: 1;
    }
  }

  .input {
    @include interactive-text;

    border: none;
    border-radius: 0;
    display: flex;
    font-weight: 600;
    min-height: var(--search-height);
    padding: rem(12);
    vertical-align: middle;
    width: 100%;

    &::-webkit-search-cancel-button {
      display: none;
    }

    &:focus-visible {
      outline: none;
    }

    &:focus,
    &:active {
      outline: none;
    }
  }

  &[data-size='small'] {
    --search-width: 22.6875rem; // 363px
  }

  &[data-size='medium'] {
    --search-width: 23.8125rem; // 381px
  }

  &[data-size='large'] {
    --search-width: 28.125rem; // 450px
  }

  &[data-size='extra-large'] {
    --search-width: 28.875rem; // 462px
  }

  .close {
    background-color: white;
    border-radius: 0 rem(8) rem(8) 0;
    display: inline-block;
    padding: rem(4);
    vertical-align: middle;
  }
}

[data-open='false'] {
  display: none;
}
